<template>
  <div id="rtmContainer"></div>
</template>

<script>
import common from "../../api/common.js";
import "echarts-gl";

export default {
  props: ["rtMapData"],
  data() {
    return {
      geoCoordMap: {
        : [112.858449, 27.929966],
        : [112.995589, 27.87772],
        : [112.376481, 27.70019],
        : [112.533499, 27.921043],
        : [112.827505, 27.614982],
      },
      chartObj: null,
    };
  },
  watch: {
    rtMapData(val) {
      // console.log(val.data);
      this.chartObj.setOption({
        series: [
          {
            data: this.convertData(val.data),
          },
        ],
      });
    },
  },
  mounted() {
    var _this = this;
    var myChartDom = common.getById(rtmContainer);
    var myChart = _this.$echarts.init(myChartDom);
    this.chartObj = myChart;

    myChart.showLoading();
    this.$http
      .get("http://localhost:8888/citymap/data")
      .then((res) => {
        // console.log(res)
        let geoJson = res.data.cityData;

        _this.$echarts.registerMap("xiangtanshi", geoJson);
        myChart.hideLoading();
        let option = {
          title: {
            text: "",
            left: "center",
            top: 0,
            textStyle: {
              color: "#fff",
              textBorderColor: "#d81e06",
              textBorderWidth: "2",
              textShadowColor: "#d81e06",
              textShadowBlur: 2,
            },
          },
          tooltip: {
            show: true,
          },
          visualMap: {
            show: false,
            min: 0,
            max: 100,
            inRange: ["#fff", "red"],
            calculable: true,
          },
          geo3D: {
            show: true,
            map: "xiangtanshi",
            boxHeight: 30,

            regionHeight: 4, // 
            // shading: 'realistic',
            // realisticMaterial: {
            // 	detailTexture: bg, // 
            // 	textureTiling: 1, // 1
            // },
            label: {
              // 
              show: false, // () [ default: false ]
              distance: 0,
              formatter(param) {
                const city = param.name;
                return `{sty1|${city}}`;
              },
              rich: {
                sty1: {
                  color: "#ffffff",
                  fontSize: 14,
                  align: "center",
                },
              },
              textStyle: {
                // 
                color: "#ffffff", // 
                opacity: 1, // 
              },
            },
            environment: new _this.$echarts.graphic.LinearGradient(
              0,
              0,
              0,
              1,
              [
                {
                  offset: 0,
                  color: "#1e528e", // 
                },
                {
                  offset: 0.5,
                  color: "#728a7c", // 
                },
                {
                  offset: 1,
                  color: "#95a2ff", // 
                },
              ],
              false
            ),
            itemStyle: {
              //  
              color: "#0090FF", // 
              // color: '#00F6FF',
              opacity: 1, //  [ default: 1 ]
              borderWidth: 1, // () [ default: 0 ]
              borderColor: "#A74A11", // [ default: #333 ]
            },
            emphasis: {
              //  hover  ( labelitemStyle )
              label: {
                // label
                show: true,
                textStyle: {
                  color: "#fff", //  
                  fontSize: 15, //  
                },
              },
            },
            light: {
              //  shading  'color' 
              // 
              main: {
                //  globe 
                color: "#ffeeee", // [ default: #fff ]
                intensity: 1.1, // [ default: 1 ]
                shadow: true, //  
                alpha: 150, //  x  beta [ default: 40 ]
                beta: 70, //  y [ default: 40 ]
              },
              ambient: {
                // 
                color: "#fff", // [ default: #fff ]
                intensity: 0.5, // [ default: 0.2 ]
              },
            },
            groundPlane: {
              show: true,
              color: "#9dcaff",
            },
            viewControl: {
              projection: "perspective",
              autoRotate: false,
              autoRotateAfterStill: 2,
              autoRotateSpeed: 10,
              distance: 130,
              beta: 5,
              alpha: 20,
              minAlpha: -360,
              maxAlpha: 360,
              minBeta: -360,
              maxBeta: 360,
            },
          },
          series: [
            {
              name: "",
              type: "bar3D",
              coordinateSystem: "geo3D",
              barSize: 3,
              shading: "lambert",
              opacity: 1,
              bevelSize: 1, //
              bevelSmoothness: 10, //
              minHeight: 10,
              maxHeight: 20,
              tooltip: {
                formatter: (param) => {
                  return `<h1 style="color: #000;font-weight:900;">${param.name}${param.seriesName}</h2><div style="height: 2px;background-color: #000;margin:5px auto;"></div><span>${param.value[2]}(/)</span>`; //
                },
              },
              itemStyle: {
                color: "#00F6FF",
                textStyle: {
                  color: "#fff",
                },
              },
              emphasis: {
                label: {
                  fontSize: 17,
                },
              },
              label: {
                show: true,
                formatter: (param) => {
                  return param.value[2] + "/";
                },
                fontWeight: "bolder",
                // color: '#fff',
                // backgroundColor: '#00F6FF',
                // borderWidth: 1
              },
              data: [],
            },
            {
              name: "",
              type: "map3D",
              map: "xiangtanshi",
              regionHeight: 5, //
              // boxWidth: 70, //
              boxHeight: 80, ////
              // boxDepth: 20, //
              // // 
              // environment: new _this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              // 	offset: 0,
              // 	color: '#1e528e' // 
              // }, {
              // 	offset: 0.5,
              // 	color: '#728a7c' // 
              // }, {
              // 	offset: 1,
              // 	color: '#95a2ff' // 
              // }], false),

              label: {
                show: true,
                color: "#fff",
                fontSize: 17,
                fontWeight: 900,
                backgroundColor: "transparent",
                // borderColor: '#fff',
                // borderWidth: 1,
              },
              itemStyle: {
                //
                borderWidth: 1.6,
                borderColor: "#95FFFF",
                color: "#0090FF",
              },
              emphasis: {
                //
                label: {
                  show: true,
                },
                itemStyle: {
                  color: "#0058B8",
                },

                textStyle: {
                  // color:'blue'
                  borderWidth: 1.6,
                  borderColor: "#95FFFF",
                },
              },
              viewControl: {
                projection: "perspective",
                autoRotate: false,
                autoRotateAfterStill: 2,
                autoRotateSpeed: 10,
                distance: 130,
                beta: 5,
                alpha: 20,
                minAlpha: -360,
                maxAlpha: 360,
                minBeta: -360,
                maxBeta: 360,
                // rotateSensitivity: 0
              },
            },
          ],
        };

        myChart.setOption(option);
        window.onresize = function () {
          myChart.resize();
        };
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    convertData(data) {
      let res = [];
      for (let i = 0; i < data.length; i++) {
        let geoCoord = this.geoCoordMap[data[i].name];
        if (geoCoord) {
          res.push({
            name: data[i].name,
            value: geoCoord.concat(data[i].value),
          });
        }
      }
      return res;
    },
  },
};
</script>

<style scoped>
#rtmContainer {
  width: 100%;
  height: 593px;
}

#rtmContainer > div {
  min-height: 500px !important;
}
</style>



